html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
  font-family: "Open Sans", sans-serif;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.section {
  background-color: #FFF;
  padding: 5rem 2rem;
}

/** Media Queries **/
.header {
  background-image: url(../img/space6.jpg);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  height: 50vh;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (min-width: 1024px) {
  .header {
    background-position: center center;
    background-size: 100%;
  }
}
.header__title {
  padding: 2rem;
  backdrop-filter: blur(30px);
}
@media (min-width: 1024px) {
  .header__name {
    font-size: 5rem;
  }
}
.header__rol {
  font-size: large;
  text-transform: uppercase;
}

.technology {
  text-align: center;
}
.technology__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .technology__grid {
    margin-top: 5rem;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
  }
}
.technology__title {
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .technology__text {
    grid-column: 1/3;
    font-size: 2rem;
  }
}
.technology__list {
  overflow-y: scroll;
  width: 35.5rem;
  height: 20rem;
  scroll-behavior: smooth;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  justify-items: center;
  align-items: center;
}
@media (min-width: 768px) {
  .technology__list {
    grid-column: 3/6;
    width: 40rem;
    height: 20rem;
    overflow-y: auto;
  }
}
@media (min-width: 1024px) {
  .technology__list {
    grid-column: 4/7;
    width: 60rem;
    height: 40rem;
    overflow-y: auto;
  }
}
.technology__img {
  max-width: 10rem;
  max-height: 10rem;
}

@media (min-width: 1024px) {
  .page__flex {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    border-bottom: 1px solid #e1e1e1;
  }
}
.page__title {
  text-align: center;
  margin-bottom: 3rem;
  text-transform: uppercase;
}
.page__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 3rem;
  border-bottom: 1px solid #e1e1e1;
}
.page__info:last-child {
  margin-bottom: 0;
}
@media (min-width: 1024px) {
  .page__info {
    margin-bottom: 0;
    border-bottom: none;
  }
}
.page__img {
  transition-property: transform;
  transition-duration: 0.3s;
}
.page__img:hover {
  transform: scale(1.1);
}
@media (min-width: 1024px) {
  .page__img {
    width: 30rem;
  }
}
.page__text {
  font-size: 2.8rem;
}
@media (min-width: 1024px) {
  .page__text {
    margin-top: 1rem;
  }
}

.cv__title {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.cv__content {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .cv__content {
    justify-content: space-around;
  }
}
.cv__img {
  display: none;
}
@media (min-width: 768px) {
  .cv__img {
    display: block;
    width: 30rem;
  }
}
.cv__buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .cv__buttons {
    flex-direction: row;
    gap: 2rem;
  }
}
.cv__button {
  text-decoration: none;
  color: #000;
  font-weight: 700;
  font-size: 2rem;
  width: 100%;
  margin-bottom: 2rem;
  padding: 2rem 4rem;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
@media (min-width: 1024px) {
  .cv__button {
    margin: 0;
    text-align: center;
    width: 25rem;
  }
}
.cv__button--view {
  background-color: #ffb703;
}
.cv__button--view:hover {
  background-color: #cf9400;
  color: #333333;
}
.cv__button--download {
  background-color: #74c69d;
}
.cv__button--download:hover {
  color: #333333;
  background-color: #50b784;
}

.contact {
  margin-top: 0;
  padding-top: 0;
}
.contact__title {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.contact__flex {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.contact__logo-gmail {
  width: 5rem;
}
@media (min-width: 768px) {
  .contact__logo-gmail {
    width: 7rem;
  }
}
.contact__logo-linkedin {
  width: 10rem;
}
@media (min-width: 768px) {
  .contact__logo-linkedin {
    width: 17rem;
  }
}
.contact__logo-telefono {
  width: 5rem;
}
@media (min-width: 768px) {
  .contact__logo-telefono {
    width: 7rem;
  }
}

body {
  background-color: #e1e1e1;
}

.cards {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 2rem;
  justify-items: center;
  align-items: center;
  overflow: hidden;
}
@media (min-width: 1024px) {
  .cards {
    padding-top: 3rem;
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
  }
}
.cards__flip {
  background-color: transparent;
  width: 10rem;
  height: 14rem;
  perspective: 1000px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .cards__flip {
    width: 13rem;
    height: 18rem;
  }
}
@media (min-width: 1024px) {
  .cards__flip {
    width: 13rem;
  }
}
.cards__flip-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
.cards__flip-move .cards__flip-inner {
  transform: rotateY(180deg);
}
.cards__flip-front {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  /* Safari */
  backface-visibility: hidden;
}
.cards__flip-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  /* Safari */
  backface-visibility: hidden;
}
.cards__flip-front {
  background-color: #bbb;
  color: black;
}
.cards__flip-back {
  background-color: #e1e1e1;
  color: white;
  transform: rotateY(180deg);
}

.game {
  background-color: #282a36;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.game__title {
  color: #FFF;
  text-align: center;
  padding-top: 5rem;
}
.game__time {
  color: #FFF;
  text-align: center;
  margin: 1rem;
}

.modal {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: fixed;
  z-index: 1;
  margin: 0 auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}
.modal__content {
  background-color: #282a36;
  margin: 0 auto;
  padding: 2rem;
  border: 1px solid #888;
  width: 80%;
}
.modal__title {
  text-align: center;
  color: #FFF;
  text-transform: uppercase;
}
.modal__text {
  color: #FFF;
  text-align: center;
}
@media (min-width: 768px) {
  .modal__text {
    font-size: 1.8rem;
  }
}
.modal__content-button {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .modal__content-button {
    flex-direction: row;
    justify-content: space-evenly;
    margin-right: 5rem;
  }
}
.modal__button {
  text-decoration: none;
  border: none;
  background-color: #74c69d;
  padding: 1rem 2rem;
  color: #282a36;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2.8rem;
  cursor: pointer;
  transition-property: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .modal__button {
    padding: 0.5rem 2rem;
    font-size: 1.8rem;
  }
}
.modal__button:hover {
  background-color: #50b784;
}
.modal__button--regresar {
  margin-top: 2rem;
  background-color: #ffb703;
  text-align: center;
}
.modal__button--regresar:hover {
  background-color: #cf9400;
}
@media (min-width: 768px) {
  .modal__button--regresar {
    margin-top: 0;
  }
}